@use 'base/variables' as v;
@use 'base/mixins' as m;

.signup {
    margin-top: 5rem;
    .logo {
        @include m.gradientText(v.$purple, v.$cyan);
    }
    .container-sm {
        @include m.highlight(v.$purple, v.$cyan);
    }
    input[type="submit"] {
        @include m.button(v.$purple, v.$white)
    }
    .actions {
        a {
            color: v.$purple;
        }
    }
}