@use 'base/variables' as v;
@use 'base/mixins' as m;

.reset {
    margin-top: 5rem;
    .logo {
        @include m.gradientText(v.$blue, v.$cyan);
    }
    .container-sm {
        p {
            @include m.highlight(v.$blue, v.$cyan);
        }
    }
    input[type="submit"] {
        @include m.button(v.$blue, v.$white)
    }
    .actions {
        a {
            color: v.$blue;
        }
    }
}